*{padding:0;margin:0;border:0;}
*,*:before,*:after{-moz-box-sizing:border-box;-webkit-box-sizing:border-box;box-sizing:border-box;}
:focus,:active{outline:none;}
a:focus,a:active{outline:none; }
nav,footer,header,aside{display:block;}
html,body{  scroll-behavior: smooth; font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%;}
input,button,textarea{font-family:inherit;}
input::-ms-clear{display:none;}
button{cursor:pointer;}
button::-moz-focus-inner{padding:0;border:0;}
a,a:visited{text-decoration:none; color: inherit;}
a:hover{text-decoration:none;}
ul li{list-style:none;}
img{vertical-align:top;}
h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:inherit;}



$background__color: rgb(24, 24, 24);
$text__color:  rgb(255, 255, 255);
$light__color:  rgb(255, 255, 255);

body {
    position: relative;
    margin: 0;
    padding-bottom: 1rem;
    min-height: 100vh;
    font-family: 'Montserrat', sans-serif;
    background-color: $background__color;

    color: red;
    font-size: 36px;
    font-weight: bold;
    text-align: center;
}

*>a:focus {
    outline: 1px solid $light__color;
}

